body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (min-width: 600px) {
    .popUp {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.7);
        transition: opacity 500ms;
    }

    .box {
        margin: 100px auto;
        padding: 20px;
        background: #fff;
        width: 30%;
        height: auto;
        position: relative;
        transition: all 5s ease-in-out;
        color: #4C4C4C;
    }

    .form_title {
        font-weight: bold;
        font-size: 20px;
        text-align: center;
        border-bottom: 1px dashed black;
        padding-bottom: 2px;
    }

    .box * {
        margin-top: 10px;
        width: 99%;
    }

    .err_msg {
        color: red;
    }

    .close {
        position: absolute;
        top: 5px;
        right: 10px;
        font-size: 20px;
        font-weight: bold;
        width: 10%;
    }

    .form_btn {
        margin-left: auto;
        margin-right: auto;
    }
}

@media only screen and (max-width: 600px) {
    .popUp {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0, 0, 0, 0.7);
    }

    .box {
        margin: 100px auto;
        padding: 20px;
        background: #fff;
        width: 80%;
        height: auto;
        position: relative;
        transition: all 5s ease-in-out;
        color: #4C4C4C;
    }

    .form_title {
        font-weight: bold;
        text-align: center;
        border-bottom: 1px dashed black;
        padding-bottom: 2px;
    }

    .box * {
        margin-top: 10px;
        width: 99%;
        font-size: 25px;
    }

    .err_msg {
        color: red;
    }

    .close {
        position: absolute;
        top: 5px;
        right: 10px;
        font-weight: bold;
        width: 10%;
    }

    .form_btn {
        margin-left: auto;
        margin-right: auto;
    }
}

@media only screen and (min-width: 600px) {
    .content {
        display: grid;
        grid-template-columns: 120px 600px auto;
        grid-row-start: 3;
        margin-top: 100px;
    }

    .img_list {
        margin-top: auto;
        margin-bottom: auto;
    }

    .img_small {
        display: block;
        width: 100px;
        margin-bottom: 10px;
        /* center the image*/
        margin-left: auto;
        margin-right: auto;
    }

    .img_selected {
        border: 1px solid black;
    }

    .img_large {
        width: 500px;
        margin-left: auto;
        margin-right: auto;
    }

    .item_title {
        font-size: 25px;
        color: #6F6F6F;
    }

    .details div {
        margin-top: 20px;
        padding: 5px;
    }

    .price {
        font-size: 25px;
        color: #F9F9F9;
    }

    .item_details {
        display: grid;
        grid-template-columns: repeat(3, auto);
    }

    .span_text {
        grid-column-start: 3;
        justify-self: end;
    }

    .button {
        border: 1px solid grey;
        width: 150px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .button:hover {
        background-color: #6F6F6F;
        color: #F9F9F9;
    }

    .change_pointer {
        /* mouse change when it hovers over items with this class */
        cursor: pointer;
    }
}

@media only screen and (max-width: 600px) {

    .img_list {
        margin-top: 20px;
    }

    .img_small {
        display: inline-block;
        padding: 5px;
        width: 30vw;
        margin-bottom: 10px;
        /* center the image*/
        margin-left: auto;
        margin-right: auto;
    }

    .img_selected {
        border: 1px solid black;
    }

    .img_large {
        width: 95vw;
        margin-left: auto;
        margin-right: auto;
    }

    .item_title {
        font-size: 30px;
        color: #6F6F6F;
    }

    .details div {
        margin-top: 20px;
        padding: 5px;
    }

    .price {
        font-size: 50px;
        color: #F9F9F9;
    }

    .item_details {
        display: grid;
        grid-template-columns: repeat(3, auto);
    }

    .span_text {
        grid-column-start: 3;
        justify-self: end;
    }

    .button {
        border: 1px solid grey;
        width: 150px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
    }
    
    .button:active {
        background-color: #6F6F6F;
        color: #F9F9F9;
    }


}

@media only screen and (min-width: 600px) {
    header {
        display: grid;
        grid-template-rows: 30px 60px;
    }

    .user_profile {
        display: grid;
        grid-template-columns: auto 100px 100px;
        grid-column-gap: 20px;
        -webkit-column-gap: 20px;
                column-gap: 20px;
    }

    .profile_block {
        justify-self: end;
        display: inline-block;
    }

    #signUp {
        grid-column-start: 2;
    }

    .neg_bar {
        padding-top: 10px;
        display: grid;
        grid-template-columns: repeat(4, auto);
        border-top: 2px solid #F0EFEF;
    }

    .block_elmt {
        display: inline-block;
        padding: 5px;
    }

    #title {
        font-size: 40px;
        font-family: "Georgia", serif;
        color: #353535;
    }

    .menu {
        grid-column-start: 4;
        justify-self: end;
    }

    .bag_num {
        color: red;
    }
}
@media only screen and (max-width: 600px) {

    .user_profile {
        position: absolute;
        right: 2vw;
        color: grey;
    }
    
    .profile_block {
        display: inline-block;
        padding-left: 30px;
    }

    #title {
        font-size: 50px;
        font-family: "Georgia", serif;
        color: #FFFFFF;
        background-color: black;
        padding-top: 20px;
    }

    .bag_num {
        color: red;
    }
}
html {
    background-color: #E2E2E2;
}

@media only screen and (min-width: 600px) {
    html {
        padding-left: 40px;
        padding-right: 40px;
    }

    .App{
        font-family: "Verdana", Sans-serif;
        display: grid;
        grid-template-rows: auto 100px auto;
        color: #818181;
    }

    p {
        white-space: pre-line;
        line-height: 30px;
    }

    .sub_titles div:hover, .menu div:hover {
        color: #282626;
    }

    .underline {
        border-bottom: 2px solid #F0EFEF;
    }
}


@media only screen and (max-width: 600px) {
  .App {
      width: 100%;
      font-size: 20px;
      margin-bottom: 10px;
  }
}
